/* ------------ Theme variable fallbacks ------------ */
:root {
  --bg: #0f0f10;
  --card-bg: #181818;
  --card-contrast: #0f1720;
  --text-color: #e6eef8;
  --muted: #9aa6b2;
  --accent-color: #4dabf7;
  --success-color: #22c55e;
  --danger-color: #ef4444;
}

/* ------------ Container ------------ */
.contact-section {
  display: flex;
  justify-content: center;
  padding-top: 6rem;          /* tighter under navbar */
  padding-bottom: 2.5rem;
  background: transparent;
  color: var(--text-color);
}

/* ------------ Card ------------ */
.contact-card {
  background-color: var(--card-bg);
  padding: 1rem;              /* reduced padding */
  border-radius: 12px;
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.28);
  transition: transform 150ms ease, box-shadow 150ms ease;
  color: var(--text-color);
  width: 100%;
  max-width: 680px;           /* reduced width */
  margin: 0 auto;
}

.contact-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.34);
}

/* ------------ Titles ------------ */
.get-in-touch-title,
.contact-title {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
}

.details-title {
  font-size: 1.05rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.detail-line {
  margin-bottom: 0.5rem;
  font-size: 0.98rem;
  line-height: 1.5;
}

/* ------------ Links ------------ */
.contact-link {
  color: var(--accent-color);
  text-decoration: none;
}

.contact-link:hover {
  text-decoration: underline;
}

/* ------------ Divider ------------ */
.details-box {
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.04);
}

/* ------------ Form ------------ */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

label {
  display: flex;
  flex-direction: column;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--muted);
}

input,
textarea {
  width: 100%;
  padding: 0.6rem 0.75rem;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.06);
  background-color: var(--card-contrast);
  color: var(--text-color);
  margin-top: 0.35rem;
  font-size: 0.95rem;
}

textarea {
  min-height: 110px;          /* reduced height */
  resize: vertical;
}

input:focus,
textarea:focus {
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px rgba(77, 171, 247, 0.08);
  outline: none;
}

/* ------------ Centered Button ------------ */
.send-btn {
  padding: 0.6rem 1rem;
  min-width: 160px;
  border: none;
  border-radius: 10px;
  background-color: var(--accent-color);
  color: white;
  font-weight: 700;
  cursor: pointer;
  transition: background-color 120ms ease, transform 60ms ease;
  align-self: center;         /* centered button */
}

.send-btn:hover {
  background-color: #1c8de0;
  transform: translateY(-1px);
}

/* ------------ Responsive ------------ */
@media (max-width: 520px) {
  .contact-card {
    padding: 0.9rem;
  }

  .send-btn {
    width: 100%;
  }
}
